/* @flow */
import React, { Component } from 'react'
import { Container, Row, Col, Collapse, Button, CardBody, Card, CardHeader } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import classNames  from 'classnames';
import './faq.css'


class Faqs extends Component {
  constructor(props){
    super(props);
    console.log('nnnnnnnnnnnnnn',this)
    this.toggleAccordion = this.toggleAccordion.bind(this);
    this.state = {
      accordion :[false,false,false,false,false,false,false]
    };
  }
  toggleAccordion(tab) {

    console.log(this.state)
    const prevState = this.state.accordion;
    const state = prevState.map((x, index) => tab === index ? !x : false);
    this.setState({
      accordion: state
    });
  }
  render(){
    return(
      <div id="faqs" className="faqs-section">
      <Container className="py-5">
        <Row>
          <Col>
            <h1 className="display-2 headline" style={{ color: '#00d0ff', fontWeight: 'bold' }}>
              <FormattedMessage id="askares.title" />
            </h1>
            <p className="lead sub-title" style={{ color: '#00d0ff' }}>
              <FormattedMessage
                id="askares.subtitle"
              />
            </p>
          </Col>
          </Row>
          <Row>
            <Col md={6}>
            <div id="accordion">
                    <Card className="accordion-card">
                      <CardHeader id="headingOne" className="accordion-header-style">
                        <Button block color="link" className="text-left m-0 p-0 accordion-btn" onClick={() => this.toggleAccordion(0)} aria-expanded={this.state.accordion[0]} aria-controls="collapseOne">
                          <span className="m-0 p-0"><FormattedMessage id="faq.title1"/></span>
                          <i className={classNames('fa pull-right', { 'fa-caret-down': !this.state.accordion[0], 'fa-caret-up': this.state.accordion[0] })}></i>
                        </Button>
                      </CardHeader>
                      <Collapse isOpen={this.state.accordion[0]} data-parent="#accordion" id="collapseOne" aria-labelledby="headingOne">
                        <CardBody >
                          <p><FormattedMessage id="faq.subtitle1"/></p>
                        </CardBody>
                      </Collapse>
                    </Card>
                    <Card className="accordion-card">
                      <CardHeader id="headingTwo" className="accordion-header-style">
                        <Button block color="link" className="text-left m-0 p-0 accordion-btn" onClick={() => this.toggleAccordion(1)} aria-expanded={this.state.accordion[1]} aria-controls="collapseTwo">
                        <span className="m-0 p-0"><FormattedMessage id="faq.title2"/></span>
                        <i className={classNames('fa pull-right', { 'fa-caret-down': !this.state.accordion[1], 'fa-caret-up': this.state.accordion[1] })}></i>
                        </Button>
                      </CardHeader>
                      <Collapse isOpen={this.state.accordion[1]} data-parent="#accordion" id="collapseTwo">
                        <CardBody>
                        <p><FormattedMessage id="faq.subtitle2"/></p>
                        </CardBody>
                      </Collapse>
                    </Card>
                    <Card className="accordion-card">
                      <CardHeader id="headingThree" className="accordion-header-style">
                        <Button block color="link" className="text-left m-0 p-0 accordion-btn" onClick={() => this.toggleAccordion(2)} aria-expanded={this.state.accordion[2]} aria-controls="collapseThree">
                        <span className="m-0 p-0"><FormattedMessage id="faq.title3"/></span>
                        <i className={classNames('fa pull-right', { 'fa-caret-down': !this.state.accordion[2], 'fa-caret-up': this.state.accordion[2] })}></i>
                        </Button>
                      </CardHeader>
                      <Collapse isOpen={this.state.accordion[2]} data-parent="#accordion" id="collapseThree">
                        <CardBody>
                          <p><FormattedMessage id="faq.subtitle3"/></p>
                        </CardBody>
                      </Collapse>
                    </Card>
                  </div>
            </Col>
            <Col md={6}>
            <div id="accordion">
                    <Card className="accordion-card">
                      <CardHeader id="headingFour" className="accordion-header-style">
                        <Button block color="link" className="text-left m-0 p-0 accordion-btn" onClick={() => this.toggleAccordion(3)} aria-expanded={this.state.accordion[3]} aria-controls="collapseFour">
                        <span className="m-0 p-0"><FormattedMessage id="faq.title4"/></span>
                        <i className={classNames('fa pull-right', { 'fa-caret-down': !this.state.accordion[3], 'fa-caret-up': this.state.accordion[3] })}></i>
                        </Button>
                      </CardHeader>
                      <Collapse isOpen={this.state.accordion[3]} data-parent="#accordion" id="collapseFour" aria-labelledby="headingOne">
                        <CardBody >
                          <p><FormattedMessage id="faq.subtitle4"/></p>
                        </CardBody>
                      </Collapse>
                    </Card>
                    <Card className="accordion-card">
                      <CardHeader id="headingFive" className="accordion-header-style">
                        <Button block color="link" className="text-left m-0 p-0 accordion-btn" onClick={() => this.toggleAccordion(4)} aria-expanded={this.state.accordion[4]} aria-controls="collapseFive">
                        <span className="m-0 p-0"><FormattedMessage id="faq.title5"/></span>
                        <i className={classNames('fa pull-right', { 'fa-caret-down': !this.state.accordion[4], 'fa-caret-up': this.state.accordion[4] })}></i>
                        </Button>
                      </CardHeader>
                      <Collapse isOpen={this.state.accordion[4]} data-parent="#accordion" id="collapseFive">
                        <CardBody>
                        <p><FormattedMessage id="faq.subtitle5"/></p>
                        </CardBody>
                      </Collapse>
                    </Card>
                    <Card className="accordion-card">
                      <CardHeader id="headingSix" className="accordion-header-style">
                        <Button block color="link" className="text-left m-0 p-0 accordion-btn" onClick={() => this.toggleAccordion(5)} aria-expanded={this.state.accordion[5]} aria-controls="collapseSix">
                        <span className="m-0 p-0"><FormattedMessage id="faq.title6"/></span>
                          <i className={classNames('fa pull-right', { 'fa-caret-down': !this.state.accordion[5], 'fa-caret-up': this.state.accordion[5] })}></i>
                        </Button>
                      </CardHeader>
                      <Collapse isOpen={this.state.accordion[5]} data-parent="#accordion" id="collapseSix">
                        <CardBody>
                          <p><FormattedMessage id="faq.subtitle6"/></p>
                        </CardBody>
                      </Collapse>
                    </Card>
                  </div>
            </Col>
        </Row>
      </Container>
    </div>
    )
  }
}

export default Faqs



