import React from 'react'
import { Container, Row, Col } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom';
import './wallet.css'

const AresWallet = () => (
  <div className="areswallet-section">
    <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
    <Container className="py-5  xs-py-1">
      <Row>
        <Col md={6} lg={6}>
          <h1 className="display-2 headline" style={{ color: '#7400a4', fontWeight: 'bold' }}>
            <FormattedMessage id="ares.wallet.title" />
          </h1>
          <p className="" style={{ color: '#7400a4' }}>
            <FormattedMessage
              id="ares.wallet.subtitle"
            />
          </p>
          <div className="button-maintain pt-80 pb-20" style={{float: 'left'}}>
                <Link to="/wallet" className="btn button-6 text-capitalize">
                    <FormattedMessage id="learn.more" />                                                               
                </Link>
            </div>
        </Col>
        <Col md={6} lg={6}>
          <div className="landing__code-img">
              <img src="/images/wallet.png" className="landing__img landing__img--over-right" alt="" />
          </div>
        </Col>
        </Row>
    </Container>
  </div>
)

export default AresWallet



