import React from 'react'
import { FormattedMessage } from 'react-intl'
import { Container, Row, Col } from 'reactstrap'
import Profile from './profile'
import './index.css'
import team1 from './TeamData-1'
import team2 from './TeamData-2'
import { Timeline } from 'react-twitter-widgets'
import { Link } from 'react-router-dom'



class Team extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      backdrop: false,
    }
  }

  toggleBackdrop = backdrop => {
    this.setState({ backdrop })
  }

  render() {
    return (
      <div id="about-us" className="team-section">
        <Container className="pt-5 team-container">
          <Row>
            <Col>
              <h1 className="display-2 headline" style={{ color: '#fff' }}>
                <FormattedMessage id="about.title" />
              </h1>
              <p className="lead sub-headline" style={{ color: '#fff' }}>
                <FormattedMessage id="about.subtitle" />
              </p>
            </Col>
          </Row>
          <Row  className="team-profile-row">
            <Col md={6} sm={8}>
              <Row>
                {team1.map(({ name, title, imageSrc, intro, linkedIn }) => (
                  <React.Fragment>
                    <Col key={name} md={6} className="px-3 py-3">
                      <Profile
                        name={name}
                        title={title}
                        src={imageSrc}
                        intro={intro}
                        linkedIn={linkedIn}
                        toggleBackdrop={this.toggleBackdrop}
                      />
                    </Col>
                    </React.Fragment>
                        ))}
                   {team2.map(({ name, title, imageSrc, intro, linkedIn }) => (
                  <React.Fragment>
                    <Col key={name} md={6} className="px-3 py-3">
                      <Profile
                        name={name}
                        title={title}
                        src={imageSrc}
                        intro={intro}
                        linkedIn={linkedIn}
                        toggleBackdrop={this.toggleBackdrop}
                      />
                    </Col>
                    </React.Fragment>
                        ))}
              </Row>
              <Row>
              <Col md={12} className="text-center">
                <Link to='/about#team' className="btn allview-button">
                <FormattedMessage id="about.all.team.and.advisors"/>
              </Link>
              </Col>
              </Row>
            </Col> 
            <Col md={3} className="sm-hide">
              <Row>
                <Col key={name} md={3} className="px-3 py-3"></Col>
              </Row>
            </Col>
            <Col md={3} sm={4}>
              <Row>
                <Col key={name} md={12} className="px-3 py-3">
                  <div className="right-pane xs-hide">
                    <Timeline 
                    dataSource={{
                      sourceType: 'profile',
                      screenName: 'arestech_io',
                    }}
                    options={{ 
                      username: 'arestech_io',
                      height: '800px',
                    }}
                    />
                  </div>
                  <div className="right-pane md-hide">
                    <Timeline 
                    dataSource={{
                      sourceType: 'profile',
                      screenName: 'arestech_io',
                    }}
                    options={{ 
                      username: 'arestech_io',
                      height: '462px',
                    }}
                    />
                  </div>
                </Col>
              </Row>
            </Col>
            </Row>
        </Container>
        <div className={`backdrop + ${this.state.backdrop ? ' show' : ''}`} />
      </div>
    )
  }
}

export default Team



