import React from 'react'
import { Container, Row, Col, Card, CardBody } from 'reactstrap'
import { FormattedHTMLMessage, FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom';
import './index.css'

const Bubble = ({ name, src }) => (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        width: '100px',
        height: '100px',
        borderRadius: '50%',
        backgroundColor: '#fff',
        padding: '20px',
      }}
    >
    <React.Fragment>
        <img alt={name} src={src} width="100%" />
    </React.Fragment>
    </div>
)

const Title = ({ text1, color1, text2, color2 }) => (
  <h2>
    <span style={{ color: color1 }}>{text1}</span>
    <span style={{ color: color2 }}>{text2}</span>
  </h2>
)

const Description = ({ children }) => (
  <p style={{ color: '#fff',  fontSize: 'larger' }}>{children}</p>
)

const Connect = () => (
  <div className="connect-section">
    <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
    <Container className="pt-5">
      <Row>
        <Col>
          <h1 className="display-2 headline" style={{ color: '#ff6427' }}>
            <FormattedMessage id="aresconnect.title" />
          </h1>
          <p className="sub-title" style={{ color: '#ff6427' }}>
            <FormattedMessage id="aresconnect.subtitle" />
          </p>
        </Col>
      </Row>
      <Row>
        <Col className="py-5" md={6} sm={6}>
          <img src="/images/connect.png" className="connect-img" alt="" />
        </Col>
        <Col md={1} className="sm-hide">
        </Col>
        <Col className="py-5" md={5} sm={6}>
          <Card className="eco-card connect card-style">
            <CardBody className="card-layout xs-card-layout">
             <Bubble name="connect" src="/images/icon-connect.png" />
              <div className="pl-20">
                <Title text1="ares" color1="#ff6427" text2="connect" color2="#ffb158" />
                <Description>
                  <FormattedHTMLMessage id="aresconnect.description" />
                </Description>
                <div  className="connectlink-button">
                <Link to="/aresconnect" className="btn aresconnect-button-1">
                  <FormattedHTMLMessage id="aressuite.readmore" />
                </Link>
                </div>
              </div>
            </CardBody>
          </Card>
          <Row style={{marginTop: '20px'}}>

                <Col className="pt-3 " md={5}>
                    <a
                        style={{ textDecoration: 'none' }}
                        href="https://twitter.com/arestech_io"
                        rel="noopener noreferrer"
                        target="_blank"
                    >
              <button type="submit" className="btn aresconnect-button-3">
                 <i className="fa fa-twitter" aria-hidden="true" style={{paddingRight: '10px' }}></i>
                  <FormattedHTMLMessage id="connect.twitter" />
                </button>
                    </a>
                </Col>
                <Col className="pt-3 " md={7}>
                    <a
                        style={{ textDecoration: 'none' }}
                        href="https://medium.com/ares-tech"
                        rel="noopener noreferrer"
                        target="_blank"
                    >
              <button type="submit" className="btn aresconnect-button-3">
              <i className="fa fa-paper-plane" aria-hidden="true" style={{paddingRight: '10px' }}></i> 
                  <FormattedHTMLMessage id="connect.blog" />
                </button>
                    </a>
                </Col>
                </Row>
          
        </Col>
      </Row>
    </Container>
  </div>
)

export default Connect



