import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import 'font-awesome/css/font-awesome.css'
import '../style/custom.css'


import Contact from '../components/Contact'
import GalleryTeaser from '../components/GalleryTeaser'
import AresWallet from '../components/AresWallet'
import Media from '../components/Media'
import Partners from '../components/Partners'
import Suite from '../components/Suite'
import Arena from '../components/Arena'
import Connect from '../components/Connect'
import Faqs from '../components/Faqs'
import JoinUs from '../components/JoinUs'
import Road from '../components/Road'
import TokenDetail from '../components/TokenDetail'
import Team from '../components/Team'
import TokenSale from '../components/TokenSale'
import TokenUsage from '../components/TokenUsage'
import MainLayout from '../util/mainLayout'
import IntlProvider from '../util/intlProvider'
import NavBar from '../components/Navbar'
import '../style/custom.css'
import Footer from '../components/Footer/Footer'
import config from '../../data/SiteConfig'


const Section = ({ children, className }) => <div className={className}>{children}</div>

const Main = ({ langKey, messages }) => (
  <IntlProvider langKey={langKey} messages={messages}>
    <div>
      <NavBar langKey={langKey} />
      <MainLayout langKey={langKey} theme="main" footerTheme="footer">
        <Section>
          <GalleryTeaser />
        </Section>
        <Section>
          <AresWallet />
        </Section>
        <Section>
          <Suite />
        </Section>
        <Section>
          <Arena />
        </Section>
        <Section>
          <Connect />
        </Section>
        <Section>
          <Faqs />
        </Section>
        <Section>
          <Team />
        </Section>
        <Section>
          <JoinUs/>
        </Section>
         <Section>
          <Road />
        </Section>
        <Section>
          <Media />
        </Section>
        <Section>
          <Partners />
        </Section>
        <Section>
          <TokenDetail />
        </Section>
        <Section>
          <TokenSale />
        </Section>
        <Section>
          <TokenUsage />
        </Section>
        <Section>
          <Contact />
        </Section>
        <Footer config={config} langKey={langKey} />
      </MainLayout>
    </div>
  </IntlProvider>
)

export default Main



