import React from 'react'
import { Container, Row, Col, Card, CardBody } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'

const Media = () => (
  <div id="media" className="media-section" style={{ paddingTop: '10rem', color: '#9B9B9B', position: 'relative' }}>
    <Container>
      <Row>
        <Col>
          <h1 className="display-2 headline" style={{ color: '#fff', fontWeight: 'bold' }}>
            <FormattedMessage id="media.title" />
          </h1>
          <p className="lead" style={{ color: '#fff' }}>
            <FormattedMessage id="media.subtitle" />
          </p>
        </Col>
      </Row>
      <Row>
        <Col md={12}>
      <Card className="media-1">
        <CardBody className="media-item">
            <a
                style={{ textDecoration: 'none' }}
                href="https://hacked.com/blockchain-utilitarianism-solutions-people-can-use/"
                rel="noopener noreferrer"
                target="_blank"
            >
          <h3>
            <FormattedMessage id="media.item.berlinvalley.intro"/>
          </h3>
            </a>
          <div className="p-1" style={{ backgroundColor: '#fff' }}>
            <img src="/images/hacked.png" alt="hacked" width="180" />
          </div>

        </CardBody>
      </Card>
      </Col>
      <Col md={12}>
      <Card className="media-2">
        <CardBody className="media-item">
            <a
                style={{ textDecoration: 'none' }}
                href="https://www.influencive.com/we-dont-need-more-icos-we-need-more-workable-frameworks-for-development/"
                rel="noopener noreferrer"
                target="_blank"
            >
          <h3>
            <FormattedMessage id="media.item.gruenderszene.intro"/>
          </h3>
            </a>
          <div className="p-1">           
           <img src="/images/influencive.png" alt="Gruenderszene" width="210" />
          </div>
        </CardBody>
      </Card>
      </Col>
      <Col md={12}>
      <Card className="media-3">
        <CardBody className="media-item">
            <a
                style={{ textDecoration: 'none' }}
                href="https://www.cryptoninjas.net/2018/09/24/ares-tech-and-bigchaindb-collaborate-for-blockchain-gaming-use-cases/"
                rel="noopener noreferrer"
                target="_blank"
            >
          <h3>
            <FormattedMessage id="media.item.blockchain.intro"/>
          </h3>
            </a>
          <div className="p-1">  
          <img src="/images/cryptoninja.png" alt="cryptoninja" width="200" />
          </div>
        </CardBody>
      </Card>
      </Col>
      <div className="col-md-12 text-center"  style={{ paddingTop: '50px' }}>
        <button type="submit" className="btn media-button">Read More</button>
      </div>
      </Row>
    </Container>
    <div className="media-shape-1">
      <img src="/images/media-shape-1.png" width="100%" height="100%" alt="" />
    </div>
    <div className="media-shape-2">
      <img src="/images/media-shape-2.png" width="100%" height="100%" alt="" />
    </div>
    <div className="media-shape-3">
      <img src="/images/media-shape-3.png" width="100%" height="100%" alt="" />
    </div>
    <div className="media-shape-4">
      <img src="/images/media-shape-4.png" width="100%" height="100%" alt="" />
    </div>
  </div>
)

export default Media



