/* @flow */
import React from 'react'
import { Container, Row, Col, Form, FormGroup, Label, Input, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import { chunk } from 'lodash'
import pictures from './pictures'
import ImageTeaser from '../ImageTeaser'
import { Link } from 'react-router-dom';

const GallaryTeaser = () => (
  <div className="gallery-traser-section">
    <Container className="py-5 p-md-5 xs-py-1">
      <Row>
      <div id="live-chat" className="live-chat-transition ">
          <div id="chat-label" className="live-chat-transition ">
            <div className="live-chat-galaxie live-chat-transition">
            <h4>
              Want regular updates on Ares ?
              <img src="/images/triangle-down.svg" alt="live-chat-up-arrow" className="live-chat-arrow live-chat-transition live-chat-up-arrow hide-up-arrow"/>
              <img src="/images/triangle-up.svg" alt="live-chat-down-arrow" className="live-chat-arrow live-chat-transition live-chat-down-arrow show-down-arrow"/>
              </h4>
            </div>
          </div>
          <div style={{padding: '30px', paddingTop: '0px'}}>
            <h3 className="chat-heading">Get regular <br/>updates on Ares</h3>
            <p>All the latest news and product developments straight to your inbox.</p>
            <Form>
              <FormGroup>
              <Label className="chat-label">Enter your email below:</Label>
                <Input type="email" name="email" id="exampleEmail" placeholder="Email Address" />
              </FormGroup>
              <Button className="chat-button">Send Me Updates</Button>
            </Form>
          </div>
      </div>
        <Col>
          <h1 className="display-2 headline" style={{ color: '#ff6427', fontWeight: 'bold' }}>
            <FormattedMessage id="update.title" />
          </h1>
          <p className="" style={{ color: '#ff6427' }}>
            <FormattedMessage
              id="update.subtitle"
            />
          </p>
        </Col>
      </Row>
      {chunk(pictures, 3).map((row, index) => (
        // eslint-disable-next-line
        <Row key={index}>
          {row.map(pic => (
            <ImageTeaser
              key={pic.src}
              src={pic.src}
              alt={pic.title}
              title={pic.title}
              desc={pic.description}
              link={pic.link}
            />
          ))}
        </Row>
      ))}
      <div className="col-md-12 text-center">
        <Link to='/about' className="btn latest-readmore-button">
        <FormattedMessage id="latest.update.readmore.button"/>
       </Link>
      </div>
    </Container>
  </div>
)

export default GallaryTeaser



