import React from 'react'
import { Container, Row, Col, Card, CardBody } from 'reactstrap'
import { FormattedHTMLMessage, FormattedMessage } from 'react-intl'
import { Link } from 'react-router-dom';
import './index.css'

const Bubble = ({ name, src }) => (
    <div
      style={{
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        width: '100px',
        height: '100px',
        borderRadius: '50%',
        backgroundColor: '#fff',
        padding: '20px',
      }}
    >
    <React.Fragment>
        <img alt={name} src={src} width="100%" />
    </React.Fragment>
    </div>
)

const Title = ({ text1, color1, text2, color2 }) => (
  <h2>
    <span style={{ color: color1 }}>{text1}</span>
    <span style={{ color: color2 }}>{text2}</span>
  </h2>
)

const Description = ({ children }) => (
  <p style={{ color: '#fff',  fontSize: 'larger' }}>{children}</p>
)

const Arena = () => (
  <div className="arena-section">
    <div className="background-container">
      <div className="custom-shape custom-shape-1" />
    </div>
    <Container className="pt-5">
      <Row>
        <Col>
          <h1 className="display-2 headline" style={{ color: '#7400a4' }}>
            <FormattedMessage id="aresarena.title" />
          </h1>
          <p className="sub-title" style={{ color: '#7400a4' }}>
            <FormattedMessage id="aresarena.subtitle" />
          </p>
        </Col>
      </Row>
      <Row>
        <Col className="py-5" md={6} sm={6}>
          <img src="/images/arena.png" className="arena-img" />
        </Col>
        <Col md={1} className="sm-hide">
        </Col>
        <Col className="py-5" md={5} sm={6}>
          <Card className="eco-card arena card-style">
            <CardBody className="card-layout xs-card-layout">
              <Bubble name="arena" src="/images/icon-arena.png" />
              <div className="pl-20">
                <Title text1="ares" color1="#7400a4" text2="arena" color2="#ce38da" />
                <Description>
                  <FormattedHTMLMessage id="aresarena.description" />
                </Description>
                <div className="arenalink-button">
                <Link to="/aresarena" className="btn aresarena-button-1">
                  <FormattedHTMLMessage id="aressuite.readmore" />
                </Link>
                </div>
              </div>
            </CardBody>
          </Card>
          <Row style={{marginTop: '20px'}}>

                <Col className="pt-3 " md={5}>
                <button type="submit" className="btn aresarena-button-3">
                  <i className="fa fa-github" aria-hidden="true" style={{paddingRight: '10px' }}></i> 
                  <FormattedHTMLMessage id="aressuite.github" />
                </button>
                </Col>
                <Col className="pt-3 " md={7}>
              <button type="submit" className="btn aresarena-button-3">
                  <i className="fa fa-paper-plane" aria-hidden="true" style={{paddingRight: '10px' }}></i> 
                  <FormattedHTMLMessage id="aressuite.telegramDev" />
                </button>
                </Col>
                </Row>
        </Col>
      </Row>
    </Container>
  </div>
)

export default Arena



